export const requestedReservationStatus = (state) => {
  return state.requestedReservationStatus
}

export const deletedReservationStatus = (state) => {
  return state.deletedReservationStatus
}

export const getReservations = (state) => {
  return state.reservations
}

export const getWaitingList = (state) => {
  return state.waitingList
}



